
import Navbar from './navbar/index'
import Home from './home2/index'
import 'bootstrap/dist/css/bootstrap.min.css';
import Curriculo from './curriculo';
import './css/css.css'
import { useState , useEffect} from 'react';
//import Logo from "./logo.svg"
//import Robot from "./img/Android_robot2p.png"

function App() {
    let [cont,setCont]= useState()
    // let [styleBase,setStyle] = useState()
    let pages = [
    <Home/>,
    <Curriculo/>
    ]
    let pageReq = (param)=>{
   
        let local = (!param)?window.location.href.split('#'):param.split('#')
    
        switch (local[1]){
            case'Home':
                return 0;
            case 'Resume':
                return 1
            default: return 0
        }
        
    
    }
    let pUp = (param)=> {
        const result = pageReq(param)
        setCont(result)
    }
    let sUp = (param)=>{
        document.body.className = param
        
    }
    useEffect(() => {
        // eslint-disable-next-line 
        // pUp(null)
        document.body.className = "dark_modify"
        console.log("Isso ser executado uma vez!");
      }, []);

    return(
        <>
            <Navbar   pOnClick={(param)=>pUp(param)} sOnClick={(param)=>sUp(param)}/>
            <div>
            {pages[cont?cont:0]}
            </div>

        </>

    )

}

export default App;
